.departments_container {
    padding-top: 20px;
}

#f_select_department, #f_select_university {
    width: 300px;
    height: 32px;
    position: relative;
    cursor: pointer;
    border-radius: 4px;
    border: 1px solid #d9d9d9;
    padding-left: 8px;
    padding-right: 8px;
}

#f_select_department:focus, #f_select_university:focus {
    border-color: #40a9ff;
    outline: 0;
    -webkit-box-shadow: 0 0 0 2px rgba(24, 144, 255, 0.2);
    box-shadow: 0 0 0 2px rgba(24, 144, 255, 0.2);
    border-right-width: 1px !important;
}

select:after {
    border-color: #40a9ff;
}

.bi_form {
    display: grid;
    -ms-flex-pack: center;
    justify-content: center;
    grid-template-rows: 30px 30px 30px 30px;
    grid-row-gap: 10px;
}

.bi_container {
    display: grid;
    grid-column-gap: 10px;
    grid-template-columns: 1fr 1fr;
}

.bi_rules {
    font-family: arial, serif;
    font-size: 16px;
}