.LogEmail{
    position: absolute;
    width: 50%;
    height: 15%;
    top: 12%;
    left: 23%;
    margin-bottom: 2.1%;
    display: flex;
    background: #32B482;
    border-radius: 20px;
    border: none;
    font-family: Open Sans, serif;
    font-size: 18px;
    padding-left: 5%;
    color: #FFFFFF;
}
.LogPhone{
    position: absolute;
    width: 50%;
    height: 15%;
    top: 33%;
    left: 23%;
    margin-bottom: 2.1%;
    display: flex;
    background: #32B482;
    border-radius: 20px;
    border: none;
    font-family: Open Sans, serif;
