.logIn{
    position: absolute;
    width: 30%;
    height: 13%;
    left: 35%;
    top: 80%;
    font-family: Open Sans, serif;
    font-weight: 600;
    border: none;
    font-size: 18px;
    text-align: center;
    color: #F2F2F2;
    background: #32B381;
    border-radius: 20px;
}
.logIn:active{
    box-shadow: 1px 1px 1px 1px black;
}
.authContainer{
    position: absolute;
    background: white;
