

@media screen and (max-width: 2440px){
    body{
        background:#D9DADA;
    }
    #none{
        border-bottom:1px solid gray ;
        border-right: none;
    }
    .wrapper{
        position: absolute;
        background: white;
        width:90%;
        margin-left:   5%;
        margin-right: 5%;

    }
    #datta{
        font-family: Open Sans;
        font-style: normal;
        font-size: 22px;
        text-align: center;

    }
    td {
        height: 70px;
        padding-left: 130px;
        padding-right: 180px;
    }
    tr{
        line-height: 48px;
        border-bottom: 1px solid gray;
        border-spacing: 0 0px;
        border-collapse:  collapse;
    }
    #dates{
        border-bottom: 1px solid gray;
        border-spacing: 0 0px;
        border-collapse:  collapse;
        font-family: Open Sans;
        font-size: 22px;
        text-align: center;

    }
    th{
        padding-left: 25px;
        padding-right: 20px;
        line-height: 48px;
        border-right:1px solid gray;
        text-align: center;
    }
    table{
        position: absolute;
        width: 80%;
        height: 80%;
        left: 20px;
        top:50px;
        background: #FFFFFF;
        border-radius: 41px;
        margin-left: 5%;
        margin-right: 5%;
        margin-bottom: 5%;
        border: 1px solid gray;
        border-spacing: 0 0px;
    }
    th:last-child {
        border-right: none;
        border-bottom: none;
    }
    .buttons{
        width: 240px;
        height: 30px;
        background-color: #32B381;
        border-radius:6px;
        border:1px solid #32B381;
    }
    img{
        height: 25px;
        width:30%;
        align-items: stretch;
    }}
@media screen and (max-width: 768px) {
    body{
        background:#D9DADA;
    }
    #none{
        border-bottom:1px solid gray ;
        border-right: none;
    }
    .wrapper{
        background: white;

    }
    #dates{
        border-right: 1px solid gray;
        text-align: center;
        height: 3%;
    }
    table {
        left: -2%;
        position: absolute;
        width: 95%;
        height: 80%;
        background: #FFFFFF;
        border-radius: 41px;
        margin-bottom: 5%;
        border: 1px solid gray;
    }
    td {
        height: 70px;
        padding-left: 100px;
        padding-right: 100px;
    }
    tr {
        border-bottom: 1px solid gray;
        font-size: 19px;
    }
    th {
        padding-bottom: 2%;
        font-size: 15px;
        border-right: 1px solid gray;
        text-align: center;
        height: 3%;
    }
    .buttons{
        height: 26px;
        width:100%;
        padding-bottom: 2px;
        background-color: #32B381;
        border-radius:6px;
        border:1px solid #32B381;
    }
}