.green-button {
    font-size: 0.875rem;

    background-color: #1bde92;
    color: white;

    font-weight: 500;
    line-height: 1.5;
    border-radius: 4px;
    letter-spacing: 0.02857em;
    text-transform: uppercase;

    -webkit-transition-duration: 0.1s; /* Safari */
    transition-duration: 0.1s;
}

.green-button:hover {
    background-color: #1abf73;
    color: white;
}

.ButtonContainer {
    display: flex;
    justify-content: center;
    width: 100%;
}